import React from 'react';
import { Grid } from '@wix/patterns';
import { GridConfig } from '../../types';
import { useGridFeatures } from '../../hooks';

export interface AutoPatternsGridContentProps {
  configuration: GridConfig;
}

export const AutoPatternsGrid = ({
  configuration,
}: AutoPatternsGridContentProps) => {
  const {
    state,
    search,
    title,
    emptyState,
    filters,
    actionCell,
    renderItem,
    imagePlacement,
    preset,
  } = useGridFeatures(configuration);

  return (
    <Grid
      dataHook="auto-patterns-grid"
      search={search}
      title={title}
      state={state}
      actionCell={actionCell}
      filters={filters}
      emptyState={emptyState}
      imagePlacement={imagePlacement}
      preset={preset}
      renderItem={renderItem}
    />
  );
};
